import React, { useState } from "react";
import { Button, Form } from "react-bootstrap";
import Rating from "./Rating";
import { useSelector, useDispatch } from "react-redux";
import {
  clearFilter,
  sortByFastDelivery,
  sortByStock,
} from "../features/cart/cartSlice";

const Filters = () => {
  const [byRating, setByRating] = useState();

  const filterProduct = useSelector((state) => state.cart.filterProduct);
  const dispatch = useDispatch();

  return (
    <div className="filters bg-primary d-md-flex flex-column text-center text-light pt-0 pb-0 pt-md-5 px-1 px-md-4">
      <span className="title">Filter Products</span>
      <div className=" sorts d-flex justify-content-center flex-row flex-md-column flex-wrap">
        <span>
          <Form.Check
            inline
            label="Only in Stock"
            name="group1"
            type="checkbox"
            id={`inline-3`}
            onChange={() => dispatch(sortByStock(!filterProduct.byStock))}
            checked={filterProduct.byStock}
          />
        </span>
        <span>
          <Form.Check
            inline
            label="Fast Delivery Only"
            name="group1"
            type="checkbox"
            id={`inline-4`}
            onChange={() =>
              dispatch(sortByFastDelivery(!filterProduct.byFastDelivery))
            }
            checked={filterProduct.byFastDelivery}
          />
        </span>
        <span>
          <label style={{ paddingRight: 10 }}>Rating: </label>
          <Rating
            rating={byRating}
            setByRating={setByRating}
            style={{ cursor: "pointer" }}
          />
        </span>
      </div>
      <Button
        variant="success"
        onClick={() => dispatch(clearFilter())}
        className="clearFilter"
      >
        Clear Filters
      </Button>
    </div>
  );
};

export default Filters;
