import "./home.css";
import React, { Suspense, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { lazy } from "react";
import Filters from "./Filters";
import { Card, Button } from "react-bootstrap";
const SingleProduct = lazy(() => import("./SingleProduct"));

const Home = () => {
  const products = useSelector((state) => state.cart.products);
  const filterProduct = useSelector((state) => state.cart.filterProduct);
  const { sort, byStock, byFastDelivery, byRating, searchQuery } =
    filterProduct;

  const [sortedProduct, setSortedProduct] = useState([...products]);

  const transferProducts = () => {
    byStock &&
      setSortedProduct([...products].filter((byStock) => byStock.inStock));

    byFastDelivery &&
      setSortedProduct(
        [...products].filter((byFastDelivery) => byFastDelivery.fastDelivery)
      );

    byRating &&
      setSortedProduct(
        [...products].filter((Rating) => Rating.rating === byRating)
      );

    searchQuery &&
      setSortedProduct(
        [...products].filter((serach) =>
          serach.name.toLowerCase().includes(searchQuery)
        )
      );

    if (
      byFastDelivery === false &&
      byRating === 0 &&
      byStock === false &&
      searchQuery === ""
    ) {
      setSortedProduct([...products]);
    }
  };

  useEffect(() => {
    transferProducts();
  }, [sort, byStock, byFastDelivery, byRating, searchQuery]);

  return (
    <div className="home d-flex">
      <Filters />
      <div className="productContainer row m-0">
        {sortedProduct.map((product, i) => (
          <Suspense
            key={i}
            fallback={
              <div className="col-6 col-sm-4 col-xl-3">
                <Card>
                  <Card.Img variant="top" />
                  <Card.Body>
                    <Card.Title></Card.Title>
                    <Card.Subtitle
                      style={{ paddingBottom: 10 }}
                    ></Card.Subtitle>
                    <Button></Button>
                  </Card.Body>
                </Card>
              </div>
            }
          >
            <SingleProduct key={product.id} product={product} />
          </Suspense>
        ))}
      </div>
    </div>
  );
};

export default Home;
